import React from "react";
import { demoChannelTitle, demoProfilePicture } from "./utils/constants";
import { Box, CardMedia, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import { CardContent } from "@mui/material";
import { CheckCircle } from "@mui/icons-material";

const ChannelCard = ({ channelDetail }) => {
  return (
    <Box
      sx={{
        boxShadow: "none",
        borderRadius: "20px",
        display: "flex",
        justifyContent:"center",
        alignItems:'center',
        height: " 326px",
        width:{md:'320px' , xs:'356px'},
        margin:'auto'
      }}
    >
      <Link to={`/channel/${channelDetail?.id?.channelId}`}>
        <CardContent
          sx={{
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            textAlign: "center",
            color: "#fff",
          }}
        >
          <CardMedia
            image={
              channelDetail?.snippet?.thumbnails?.high?.url ||
              demoProfilePicture
            }
            alt={channelDetail?.snippet?.title}
            sx={{
              borderRadius: "50%",
              height: "180px",
              width: "180px",
              mb: 2,
              border: "1px solid #e3e3e3",
            }}
          />
          <Typography
            variant="h6"
            style={{ display: "flex", alignItems: "center" , textDecoration:'none', margin:'auto'}}
          >
            {channelDetail?.snippet?.title || demoChannelTitle}
            <CheckCircle
              sx={{ fontSize: 14, ml: "5px", mt:'6px', color: "gray" }}
            ></CheckCircle>
          </Typography>
          {channelDetail?.statistics?.subscriberCount && (
            <Typography>
                {parseInt(channelDetail?.statistics?.subscriberCount).toLocaleString()} Subscriber
            </Typography>
          )}
        </CardContent>
      </Link>
    </Box>
  );
};

export default ChannelCard;
